import React, { Component } from "react";
import PropTypes from "prop-types";

import st from "./style";
import DefaultButton from '../../ui/DefaultButton';
import CakeTahTiti from '../../images/CakeTahTiti';

class BodyError extends Component {
  
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {

    return (
      <div style={st.container}>
        <div style={st.ctnImage}>
          <CakeTahTiti />
        </div>
        <h1 style={st.number}>{this.props.number}</h1>
        <h3 style={st.title}>{this.props.title}</h3>
        <p  style={st.text}>{this.props.text}</p>
        {
          this.props.showButton?
          <DefaultButton
            style={st.button}
            label={this.props.labelButton}
            onClick={this.props.onClickButton}
          />
          :null
        }
        
      </div>
    );
  }
}

BodyError.propTypes = {
  number:PropTypes.number,
  title:PropTypes.string,
  text:PropTypes.string,
  labelButton:PropTypes.string,
  onClickButton:PropTypes.func,
  showButton:PropTypes.bool,
};
BodyError.defaultProps = {
  number:null,
  title:"",
  text:",",
  labelButton:"Tente novamente",
  onClickButton:()=>{},
  showButton:false,
};

export default BodyError;
